import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

import {ReelController} from './ReelController';

@ccclass('ReelManager')
export class ReelManager extends Component {

	public static Instance = null;

 	@property({type: ReelController})
	protected reel: ReelController[] = [];

	onLoad() {
		this.Instance = this;
	}

    start() {

    }

    update(deltaTime: number) {
        
    }

    onRunReal() {

    	for (var i = 0; i < this.reel.length; i ++) {
    		
            this.reel[i].setStartDelay(i*0.1);
            this.reel[i].setCount(30);
    		this.reel[i].moveNextStatus();
    		
	    }
    }
}

